import {defineComponent} from 'vue';
import PropTypes from '../../../_util/vue-types';
import {getMonthName} from '../util';

const CalendarHeader = defineComponent({
  props: {
    value: PropTypes.object,
    locale: PropTypes.object,
    yearSelectOffset: PropTypes.number.def(10),
    yearSelectTotal: PropTypes.number.def(20),
    // onValueChange: PropTypes.func,
    // onTypeChange: PropTypes.func,
    Select: PropTypes.object,
    prefixCls: PropTypes.string,
    type: PropTypes.string,
    showTypeSwitch: PropTypes.bool,
    headerComponents: PropTypes.array
  },
  setup($props, {emit}) {
    const onYearChange = (year) => {
      const newValue = $props.value.clone();
      newValue.year(parseInt(year, 10));
      emit('valueChange', newValue);
    };
    const onMonthChange = (month) => {
      const newValue = $props.value.clone();
      newValue.month(parseInt(month, 10));
      emit('valueChange', newValue);
    };
    const yearSelectElement = (year) => {
      const {yearSelectOffset, yearSelectTotal, prefixCls, Select} = $props;
      const start = year - yearSelectOffset;
      const end = start + yearSelectTotal;
      const SelectOption = Select.Option;
      const options = [];
      for (let index = start; index < end; index++) {
        options.push(<SelectOption value={`${index}`}>{index}</SelectOption>);
      }
      return (
          <Select
              class={`${prefixCls}-header-year-select`}
              onChange={onYearChange}
              dropdownStyle={{zIndex: 2000}}
              dropdownMenuStyle={{maxHeight: '250px', overflow: 'auto', fontSize: '12px'}}
              optionLabelProp="children"
              value={String(year)}
              showSearch={false}>
            {options}
          </Select>
      );
    };
    const monthSelectElement = (month) => {
      const {value, Select, prefixCls} = $props;
      const t = value.clone();
      const options = [];
      const SelectOption = Select.Option;
      for (let index = 0; index < 12; index++) {
        t.month(index);
        options.push(<SelectOption value={`${index}`}>{getMonthName(t)}</SelectOption>);
      }

      return (
          <Select
              class={`${prefixCls}-header-month-select`}
              dropdownStyle={{zIndex: 2000}}
              dropdownMenuStyle={{
                maxHeight: '250px',
                overflow: 'auto',
                overflowX: 'hidden',
                fontSize: '12px'
              }}
              optionLabelProp="children"
              value={String(month)}
              showSearch={false}
              onChange={onMonthChange}
          >
            {options}
          </Select>
      );
    };
    const changeTypeToDate = () => {
      emit('typeChange', 'date');
    };
    const changeTypeToMonth = () => {
      emit('typeChange', 'month');
    };


    return {
      onYearChange,
      onMonthChange,
      yearSelectElement,
      monthSelectElement,
      changeTypeToDate,
      changeTypeToMonth
    };
  },
  render() {
    const {value, locale, prefixCls, type, showTypeSwitch, headerComponents} = this;
    const year = value.year();
    const month = value.month();
    const yearSelect = this.yearSelectElement(year);
    const monthSelect = type === 'month' ? null : this.monthSelectElement(month);
    const switchCls = `${prefixCls}-header-switcher`;
    const typeSwitcher = showTypeSwitch ? (
        <span class={switchCls}>
          {type === 'date' ? (
              <span class={`${switchCls}-focus`}>{locale.month}</span>
          ) : (
              <span onClick={this.changeTypeToDate} class={`${switchCls}-normal`}>
                {locale.month}
              </span>
          )}
          {type === 'month' ? (
              <span class={`${switchCls}-focus`}>{locale.year}</span>
          ) : (
              <span onClick={this.changeTypeToMonth} class={`${switchCls}-normal`}>
                {locale.year}
              </span>
          )}
        </span>
    ) : null;

    return (
        <div class={`${prefixCls}-header`}>
          {typeSwitcher}
          {monthSelect}
          {yearSelect}
          {headerComponents}
        </div>
    );
  }
});

export default CalendarHeader;
